import { MainService } from '../../services/main.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Publicacio } from '../../classes/publicacio';
@Component({
    selector: 'app-publicacio-artwork',
    templateUrl: './publicacio-artwork.component.html',
    styleUrls: ['./publicacio-artwork.component.scss']
})
export class PublicacioArtworkComponent implements OnInit {

    @ViewChild("publicacioComponent") publicacioComponent;
    public params;

    public publicacio: Publicacio;

    constructor(
        public main: MainService,
        public router: Router,
        private route: ActivatedRoute,
    ) {
        main.newPage(this);
        
        // force route reload whenever params change;
        this.router.routeReuseStrategy.shouldReuseRoute = () => false;
        
        this.route.params.subscribe(params => this.params = params);

    }

    async ngOnInit() { }
    ngOnDestroy() { this.main.onDestroyPage(); }
}
