import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PublicacioArtworkComponent } from './publicacio-artwork.component';

const routes: Routes = [{
    path: '',
    children: [
        {
            path: '',
            component: PublicacioArtworkComponent
        }
    ]
}];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class PublicacioArtworkRoutingModule { }
